import {Injectable} from '@angular/core';

import {Storage} from '@ionic/storage';


@Injectable()
export class EmailSettingsProvider {

  constructor(private storage: Storage) {
    this.getEmailSettings().then(emailSettings => {
      if (emailSettings == null) {
        let defaultEmailSettings =
          buildEmailSettings("persoon@emailadres.nl", "Sasem informatie", "hallo Sasem stuurt een berichtje");

        this.saveEmailSettings(defaultEmailSettings);

      }
    })
  }

  public saveEmailSettings(emailSettings: EmailSettings): void {
    this.storage.set('emailSettings', JSON.stringify(emailSettings));
  };

  public getEmailSettings(): Promise<EmailSettings> {
    return Promise.resolve(this.storage.get('emailSettings')).then((rawData) => {
      return JSON.parse(rawData);
    });
  }

}

export function buildEmailSettings(to: string, subject: string, body: string) : EmailSettings {
  return  {'to': to, 'subject': subject, 'body': body }
}


export interface EmailSettings {
  to: string,
  subject: string,
  body: string
}



