import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {LoadDataForSelectedMonthProvider} from "../../providers/load-data-for-selected-month/load-data-for-selected-month";
import {ProvidedCare, ProvidedCareService} from '../../providers/provided-care-service/provided-care-service';
import {CareGiver, CareGiverProvider} from '../../providers/care-giver/care-giver';
import {EmailComposer} from '@ionic-native/email-composer';
import {EmailSettingsProvider} from "../../providers/email-settings/email-settings";
import {CARE_TYPES_WITH_DURATION, CareType} from "../../providers/care-type/care-type";
import {it} from "selenium-webdriver/testing";


@IonicPage()
@Component({
  selector: 'page-event-view',
  templateUrl: 'event-view.html',
})
export class EventViewPage {

  careGivers: CareGiver[];

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private providedCareService: ProvidedCareService,
              private careGiverProvider: CareGiverProvider,
              private loadDataForSelectedMonth: LoadDataForSelectedMonthProvider,
              private emailSettingsProvider: EmailSettingsProvider,
              private emailComposer: EmailComposer) {

    this.careGivers = [];
    careGiverProvider.getActiveCareGivers().then((loadedCareGivers) => {
      this.careGivers = loadedCareGivers;
    })

  }


  monthToShow = new Date();
  careGiverSelection = "";
  careTypeSelection: CareType;

//todo netter maken, nu domweg doogesluisd
  loadCareForMonthToShow() {
    this.loadDataForSelectedMonth.loadCareForMonthToShow(this.monthToShow,this.careGiverSelection,this.careTypeSelection);
  }

  goToPreviousMonth() {
    let newDisplayDate = new Date(this.monthToShow);
    newDisplayDate.setMonth(newDisplayDate.getMonth() - 1);
    this.monthToShow = newDisplayDate;
    this.loadCareForMonthToShow();
  }

  goToNextMonth() {
    let newDisplayDate = new Date(this.monthToShow);
    newDisplayDate.setMonth(newDisplayDate.getMonth() + 1);
    this.monthToShow = newDisplayDate;
    this.loadCareForMonthToShow();
  }


//todo netter maken, nu domweg doogesluisd
  getDays() {
    return this.loadDataForSelectedMonth.getDays();
  }

//todo netter maken, nu domweg doogesluisd
  getEvents(day) {
    return this.loadDataForSelectedMonth.getEvents(day);
  }

  ionViewDidLoad() {
    this.loadCareForMonthToShow();
  }

  showDuration(event: ProvidedCare) {
    return CARE_TYPES_WITH_DURATION.find((it) => it == event.careType);
  }
}




