import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {EmailSettings, EmailSettingsProvider} from "../../providers/email-settings/email-settings";
import {ProvidedCareService} from "../../providers/provided-care-service/provided-care-service";
import {EmailComposer} from "@ionic-native/email-composer";
import {CareGiver, CareGiverProvider} from "../../providers/care-giver/care-giver";
import {LoadDataForSelectedMonthProvider} from "../../providers/load-data-for-selected-month/load-data-for-selected-month";
import {describe, SelectableCareType, SelectableCareTypes, CareType} from "../../providers/care-type/care-type";

@IonicPage()
@Component({
  selector: 'page-send-email',
  templateUrl: 'send-email.html',
})
export class SendEmailPage {

  careGivers : CareGiver[];

  monthSelection : string;
  careGiverSelection : String;
  careTypeSelection : string;

  private monthLabel: Date;
  private monthPlusYearName: String;
  private careTypes: SelectableCareType[] = SelectableCareTypes;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private providedCareService: ProvidedCareService,
              private careGiverProvider: CareGiverProvider,
              private loadDataForSelectedMonth: LoadDataForSelectedMonthProvider,
              private emailSettingsProvider: EmailSettingsProvider,
              private emailComposer: EmailComposer,
              ) {

    this.careGivers = [];

    this.monthSelection = new Date(Date.now()-(1000*60*60*24*28)).toISOString();
    this.adjustLabel();
    this.careGiverSelection = "";
    this.careTypeSelection = "";
    careGiverProvider.getActiveCareGivers().then((loadedCareGivers) => {
      this.careGivers = loadedCareGivers;

    });
  }



  sendEmail() {
  let selectedCareType = null;
  if (this.careTypeSelection != ""){
    selectedCareType = CareType[this.careTypeSelection]
  }
     this.loadDataForSelectedMonth.loadCareForMonthToShow(this.monthLabel, this.careGiverSelection, selectedCareType)
     this.emailSettingsProvider.getEmailSettings().then((emailSettings) => {
          if (emailSettings) {
            this.sendEmailWithData(emailSettings);
          }
          else {
            console.log('email settings OFF')
          }
        });
  }

  private sendEmailWithData(emailSettings: EmailSettings) {
    let email = {
      to: emailSettings.to,
      cc: '',
      bcc: '',
      attachments: [],
      subject: emailSettings.subject,
      body: emailSettings.body + '<br>' + this.composeBody(),
      isHtml: true
    };

// Send a text message using default options
    this.emailComposer.open(email);
  }

  composeBody() {
    let body = 'Datum , Zorgsoort , Zorgverlener , duur , moment <br>';


    function displayDuration(duration: number) {
      if (!duration || duration == 0){
        return '';
      }
      return duration;
    }

    function displayMoment(moment: String) {
      if (!moment){
        return '';
      }
      return moment;
    }

    for (let day of this.loadDataForSelectedMonth.getDays()) {
      for (let event of this.loadDataForSelectedMonth.getEvents(day)){
        body = body.concat(
          day.toString() + ' ' + this.monthPlusYearName + ' , ' ,
          event.careTypeDescription + ' , ' ,
          event.careGiver.name + ' , ' ,
          displayDuration(event.duration) + ' , ' ,
          displayMoment(event.moment) + '<br>');
      }
    }
    return body;
  }

  composeEventStringForEmail(event){

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SendEmailPage');
  }

  selectCareGiver(selection) {
    let selectedCareGiver = selection;
  }

  adjustLabel() {
    this.monthLabel = new Date(this.monthSelection);
    this.monthPlusYearName = new Intl.DateTimeFormat("nl-NL",{month: "long", year: "numeric"}).format(this.monthLabel);
  }






}



