import {Component, ViewChild} from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {CareGiver, CareGiverProvider} from "../../providers/care-giver/care-giver";
import {preventDuplicatesValidatorFactory} from "../add-care-giver-modal/add-care-giver-modal";

@IonicPage()
@Component({
  selector: 'page-edit-care-giver-modal',
  templateUrl: 'edit-care-giver-modal.html',
})
export class EditCareGiverModalPage {

  careGiver: CareGiver;


  @ViewChild('nameInput') nameInput;
  private editCaregiverForm: FormGroup;
  private careGiverNameControl: FormControl;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public careGiverProvider: CareGiverProvider,
    public viewCtrl: ViewController) {

    this.careGiver = navParams.get("selectedCareGiver");

    this.careGiverNameControl = new FormControl('', Validators.compose(
      [Validators.minLength(3),
        Validators.pattern('[a-zA-Z ]*'), Validators.required, preventDuplicatesValidatorFactory(this.careGiverProvider)])
    );

    this.editCaregiverForm = new FormBuilder().group({
      'careGiverNameControl': this.careGiverNameControl
    });

  }

  editCaregiver() {

    let result = this.careGiverProvider.updateCareGiver(this.careGiver);
    result
      .then(any => this.viewCtrl.dismiss(this.careGiver))
      .catch(any => console.log("Failed to store edited CareGiver"));
  }

  cancel() {
    this.viewCtrl.dismiss();
  }


}



