import {Component, ViewChild} from '@angular/core';
import {AlertController, IonicPage, ModalController, NavController, NavParams} from 'ionic-angular';
import * as moment from 'moment';
import {
  ProvidedCareService,
  shouldThisEventBeDeleted
} from '../../providers/provided-care-service/provided-care-service';
import {DefaultCareProvider} from '../../providers/default-care/default-care'
import {CalendarComponent} from "ionic2-calendar/calendar";
import {EventViewPage} from "../event-view/event-view";
import {convertProvidedCareToCareActivity} from "../ProvidedCareConverter";
import {registerLocaleData} from '@angular/common';
import localeNL from '@angular/common/locales/nl';
import {CareGiver, CareGiverProvider} from "../../providers/care-giver/care-giver";

registerLocaleData(localeNL);

@IonicPage()
@Component({
  selector: 'page-calendar',
  templateUrl: 'calendar.html',
})
export class CalendarPage {

  @ViewChild(CalendarComponent) myCalendar:CalendarComponent;
  private allCareGiversMap = new Map<String, CareGiver>();

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private modalCtrl: ModalController,
              private alertCtrl: AlertController,
              private providedCareService: ProvidedCareService,
              private defaultCare: DefaultCareProvider,
              careGiverProvider: CareGiverProvider) {


    careGiverProvider.getAllCareGivers().then(careGivers => {
      careGivers.forEach(careGiver => this.allCareGiversMap.set(careGiver.code, careGiver));
    })

  }


  eventSource = [];

  viewTitle: string;
  selectedDay = new Date();
  isToday: boolean;
  calendar = {
    mode: 'month',
    startDay: '1',
    noEvents: "Geen gegevens",
    currentDate: new Date(),
    locale: 'nl-NL'
  };

  today() {
    this.calendar.currentDate = new Date();
  }


  addEvent() {
    let modalData = {
      careGiver: this.defaultCare.getCareGiver(this.selectedDay) ,
      careType: this.defaultCare.getCareType(this.selectedDay),
      selectedDay: this.selectedDay
    };

    let modal = this.modalCtrl.create('EventModalPage', modalData);
    modal.present();
    modal.onDidDismiss(data => {
      if (data) {
        let providedCare = {
          careGiver: data.careGiver,
          careType: data.careType,
          moment: data.moment,
          duration: data.duration,
          startTime: new Date(data.startTime),
          endTime: new Date(data.endTime),
          allDay: false
        };

        let careActivity = convertProvidedCareToCareActivity(providedCare, this.allCareGiversMap);
        let events = this.eventSource;
        events.push(careActivity);

        this.providedCareService.storeProvidedCare(providedCare);

        this.eventSource = [];
        setTimeout(() => {
          this.eventSource = events;
        });
      }
    });
  }

  onCurrentDateChanged(event) {
    var today = new Date();
    var sameYear = (today.getFullYear() === event.getFullYear());
    var sameMonth = (today.getMonth() === event.getMonth());
    var sameDay = (today.getDate() === event.getDate());
    this.isToday = (sameYear && sameMonth && sameDay);
  }

  onViewTitleChanged(title) {
    this.viewTitle = title;
  }

  onEventSelected(event) {
      let start = moment(event.startTime).format('LLLL');
      let end = moment(event.endTime).format('LLLL');

      let alert = this.alertCtrl.create({
        title: '' + event.careTypeDescription,
        subTitle: 'From: ' + start + '<br>To: ' + end,
        buttons: [
          {
            text: 'REMOVE',
            handler: () => {
              console.log('Remove clicked');
              console.log(event);
              this.removeCareActivity(event);

            }
          },
          { text: 'OK' }
        ]
      });
      alert.present();
  }

  private removeCareActivity(careToDelete) {
    this.providedCareService.removeSelectedCare(careToDelete);
    let indexOfEventToDelete = this.eventSource.findIndex(function (value) {
        return shouldThisEventBeDeleted(value,careToDelete)
    });
    this.eventSource.splice(indexOfEventToDelete,1);
    this.myCalendar.loadEvents();
  }



  onTimeSelected(ev) {
      this.selectedDay = ev.selectedTime;
    }

  toEventView(): void {
    this.navCtrl.push(EventViewPage);
  }

    ionViewDidLoad() {

      this.providedCareService.getStoredCare().then((storedCare) => {

        if (storedCare) {

          let events = this.eventSource;

          for (let providedCare of storedCare) {
            let careActivity = convertProvidedCareToCareActivity(providedCare, this.allCareGiversMap);
            events.push(careActivity);
          }

          setTimeout(() => {
            this.eventSource = events;
          });

          this.myCalendar.loadEvents();
        }

      })

    }


}



