export enum CareType {

    DOMESTIC_ASSISTANCE = "DOMESTIC_ASSISTANCE",
    GROUP_GUIDANCE = "GROUP_GUIDANCE",
    INDIVIDUAL_GUIDANCE = "INDIVIDUAL_GUIDANCE",
    NURSING = "NURSING",
    PERSONAL_CARE = "PERSONAL_CARE",
    TRANSPORTATION = "TRANSPORTATION"

}

export const CareTypes = Object.keys(CareType);

export const SelectableCareTypes : SelectableCareType[] = CareTypes.map(element => {
    return {
            careType: CareType[element],
            description: describe(CareType[element])
    };
});

export function describe(careType: CareType) : String {
    switch (careType) {
        case CareType.PERSONAL_CARE: return "Persoonlijke verzorging";
        case CareType.NURSING: return "Verpleging";
        case CareType.INDIVIDUAL_GUIDANCE: return "Begeleiding individueel";
        case CareType.GROUP_GUIDANCE: return "Begeleiding groep";
        case CareType.DOMESTIC_ASSISTANCE: return "Huishoudelijke hulp ";
        case CareType.TRANSPORTATION: return "Vervoer";

        default: return "";
    }
}

export const CARE_TYPES_WITH_DURATION: Array<CareType> = [CareType.DOMESTIC_ASSISTANCE,
  CareType.INDIVIDUAL_GUIDANCE,
  CareType.GROUP_GUIDANCE,
  CareType.NURSING,
  CareType.PERSONAL_CARE];


export interface SelectableCareType {
    careType: CareType,
    description: String
}



