import {ProvidedCare} from "../providers/provided-care-service/provided-care-service";
import {CareType, describe} from "../providers/care-type/care-type";
import {CareGiver} from "../providers/care-giver/care-giver";


  export function convertProvidedCareToCareActivity(providedCare: ProvidedCare, allCareGiversMap: Map<String, CareGiver>): CareActivity {
    return {
      careGiver: allCareGiversMap.get(providedCare.careGiver),
      careType: providedCare.careType,
      moment: providedCare.moment,
      duration: providedCare.duration,
      startTime: new Date(providedCare.startTime),
      endTime: new Date(providedCare.endTime),
      allDay: false,
      careTypeDescription: describe(providedCare.careType),
    };
  }


  export interface CareActivity {
    careGiver: CareGiver
    careType: CareType,
    moment: String,
    duration: number,
    startTime: Date,
    endTime: Date,
    allDay: boolean,
    careTypeDescription: String

  }



