import {Injectable} from '@angular/core';
import {Storage} from '@ionic/storage';
import {CareType} from "../care-type/care-type";
import {duration} from "moment";

export function shouldThisEventBeDeleted(providedCare, careToDelete: ProvidedCare) {
  return (providedCare.careGiver === careToDelete.careGiver) &&
    (providedCare.careType === careToDelete.careType) &&
    (providedCare.startTime.getTime() === careToDelete.startTime.getTime());
}

@Injectable()
export class ProvidedCareService {

  constructor(private storage: Storage) {
  }

  storeProvidedCare(providedCare: ProvidedCare): void {
    this.getStoredCare().then((storedCare) => {
      if (storedCare) {
        console.log(storedCare);
      } else {
        storedCare = [];
      }
      storedCare.push(providedCare);

      this.storage.set('care', JSON.stringify(storedCare));
    });
  }

  //

  removeSelectedCare(careToDelete: ProvidedCare): void {

    this.getStoredCare().then((storedCare) => {
      let indexOfEventToDelete = storedCare.findIndex(function (value) {
        return shouldThisEventBeDeleted(value, careToDelete)
      });
      storedCare.splice(indexOfEventToDelete, 1);
      this.storage.set('care', JSON.stringify(storedCare));
    })
  }

  getStoredCare(): Promise<ProvidedCare[]> {
    return Promise.resolve(this.storage.get('care')).then((rawData) => {
      return JSON.parse(rawData, function (key, value): any {
        if (key === "startTime") {
          return new Date(value);
        }
        return value;
      });
    });
  }

}

export interface ProvidedCare {
  careGiver: String,
  careType: CareType,
  moment: String,
  duration: number,
  startTime: Date;
  endTime: Date;
  allDay: boolean;
}


export function createDefaultProvidedCare() {
  return {
    careGiver: '',
    careType: '',
    duration: 0,
    moment: "",
    startTime: new Date().toISOString(),
    endTime: new Date().toISOString(),
    allDay: false
  };

}






