import { Injectable } from '@angular/core';
import { CareType } from '../care-type/care-type';

@Injectable()
export class DefaultCareProvider {

  constructor() {
    console.log('Hello DefaultCareProvider Provider');
  }

  getCareGiver(date: Date) {
    switch (date.getDay()) {
      case 0: return 'boshoeve';
      case 1: return 'boshoeve';
      case 2: return 'boshoeve';
      case 3: return 'boshoeve';
      case 4: return 'boshoeve';
      case 5: return 'boshoeve';
      case 6: return 'boshoeve';
    }

  }

  getCareType(date: Date): CareType {
    switch (date.getDay()) {
      case 0:
        return CareType.NURSING;
      case 1:
        return CareType.NURSING;
      case 2:
        return CareType.PERSONAL_CARE;
      case 3:
        return CareType.TRANSPORTATION;
      case 4:
        return CareType.NURSING;
      case 5:
        return CareType.NURSING;
      case 6:
        return CareType.NURSING;
    }
  }

}



