import { Injectable } from '@angular/core';

import { Storage } from '@ionic/storage';


@Injectable()
export class CareGiverProvider {

  
  constructor(private storage: Storage) {
      this.getAllCareGivers().then(careGivers => {
        if (careGivers == null || careGivers.length == 0) {
          let defaultCareGivers = [
            careGiverWithName("Ikzelf"),
          ];

          storage.set('careGivers', JSON.stringify(defaultCareGivers));

        }
      })


  }

  public getActiveCareGivers() : Promise<CareGiver[]> {
    return Promise.resolve(this.storage.get('careGivers')).then((rawData) => {
      let careGivers : CareGiver[] = JSON.parse(rawData);
      return careGivers.filter(it => it.active);
    });
  }

  public getAllCareGivers(): Promise<CareGiver[]> {
      return Promise.resolve(this.storage.get('careGivers')).then((rawData) => {
        return JSON.parse(rawData);
      });
   }


   public addCareGiver(additionalCareGiver: CareGiver): Promise<any> {
    return Promise.resolve(this.storeAdditionalCareGiver(additionalCareGiver))
  }

  public removeCareGiver(careGiverToRemove: CareGiver): Promise<any>  {
    
    return Promise.resolve(this.getAllCareGivers().then((storedCareGivers) => {
  
      let indexOfCareGiverToDelete = storedCareGivers.findIndex(function (value) {
        return value.name === careGiverToRemove.name;
      });
      storedCareGivers.splice(indexOfCareGiverToDelete, 1);

      return Promise.resolve(
        this.storage.set('careGivers', JSON.stringify(storedCareGivers))
      );
    }).catch((reason) => {
      return Promise.reject("Could not load original set of careGivers");
    }))
  }


  updateCareGiver(careGiverToUpdate: CareGiver) : Promise<any>{
    return Promise.resolve(this.storeUpdatedCareGiver(careGiverToUpdate));
  }


  private storeAdditionalCareGiver(additionalCareGiver: CareGiver) {
    return this.getAllCareGivers().then((loadedCareGivers) => {
      loadedCareGivers.push(additionalCareGiver);
      return Promise.resolve(
        this.storage.set('careGivers', JSON.stringify(loadedCareGivers))
      );
    }).catch((reason) => {
      return Promise.reject("Could not load original set of careGivers");
    });
  }

  private storeUpdatedCareGiver(careGiverToUpdate: CareGiver) {
    return this.getAllCareGivers().then( (storedCareGivers) => {

      storedCareGivers.filter(it => it.code == careGiverToUpdate.code)
        .forEach(it => {
          it.active = careGiverToUpdate.active;
          it.name= careGiverToUpdate.name;
        });

      this.storage.set('careGivers', JSON.stringify(storedCareGivers))

    });
  }
}

export function careGiverWithName(name: String) : CareGiver {
  return buildCareGiver(generateGuid(), name)
}

export function buildCareGiver(code: String, name: String) : CareGiver {
  return  {'code': code, 'name': name, 'active': true}
}


export function generateGuid() {
  let result, i, j;
  result = '';
  for (j = 0; j < 32; j++) {
    if (j == 8 || j == 12 || j == 16 || j == 20)
      result = result + '-';
    i = Math.floor(Math.random() * 16).toString(16).toUpperCase();
    result = result + i;
  }
  return result;
}

export interface CareGiver {
  code: String,
  name: String,
  active: boolean
}



