import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule } from '@ionic/storage';
import { EmailComposer } from '@ionic-native/email-composer';

import { MyApp } from './app.component';
import { WelcomePage } from '../pages/welcome/welcome';
import { CalendarPage } from '../pages/calendar/calendar';
import { SettingsPage} from "../pages/settings/settings";

import { NgCalendarModule } from 'ionic2-calendar';
import { ProvidedCareService } from '../providers/provided-care-service/provided-care-service';
import { EventViewPage} from "../pages/event-view/event-view";
import { CareGiverProvider } from '../providers/care-giver/care-giver';
import { DefaultCareProvider } from '../providers/default-care/default-care';
import { EmailSettingsProvider } from '../providers/email-settings/email-settings';
import { SendEmailPage } from "../pages/send-email/send-email";
import { LoadDataForSelectedMonthProvider } from '../providers/load-data-for-selected-month/load-data-for-selected-month';
import {EditCareGiverModalPage} from "../pages/edit-care-giver-modal/edit-care-giver-modal";
import {EditCareGiverModalPageModule} from "../pages/edit-care-giver-modal/edit-care-giver-modal.module";


@NgModule({
  declarations: [
    MyApp,
    WelcomePage,
    CalendarPage,
    SettingsPage,
    EventViewPage,
    SendEmailPage
  ],
  imports: [
    NgCalendarModule,
    BrowserModule,
    EditCareGiverModalPageModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot()
  ],

  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    CalendarPage,
    EditCareGiverModalPage,
    EventViewPage,
    SendEmailPage,
    SettingsPage,
    WelcomePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ProvidedCareService,
    Storage,
    CareGiverProvider,
    DefaultCareProvider,
    EmailComposer,
    EmailSettingsProvider,
    LoadDataForSelectedMonthProvider,
  ]
})
export class AppModule {}



