import {Component, ViewChild} from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';

import {buildCareGiver, CareGiver, CareGiverProvider, generateGuid} from '../../providers/care-giver/care-giver';
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";

@IonicPage()
@Component({
  selector: 'add-caregives',
  templateUrl: 'add-care-giver-modal.html',
})
export class AddCaregiverModalPage {

  careGiver: CareGiver;

  @ViewChild('nameInput') nameInput;
  private addCaregiverForm: FormGroup;
  private careGiverNameControl: FormControl;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public careGiverProvider: CareGiverProvider,
    public viewCtrl: ViewController) {


    this.careGiver = buildCareGiver(generateGuid(), "");


    this.careGiverNameControl = new FormControl('', Validators.compose(
      [Validators.minLength(3),
        Validators.pattern('[a-zA-Z ]*'), Validators.required, preventDuplicatesValidatorFactory(this.careGiverProvider)])
    );

    this.addCaregiverForm = new FormBuilder().group({
      'careGiverNameControl': this.careGiverNameControl
    });


  }

  ngAfterViewChecked() {
    this.nameInput.setFocus();


  }

  cancel() {
    this.viewCtrl.dismiss();
  }

  addCaregiver(form) {
    let result = this.careGiverProvider.addCareGiver(this.careGiver);
    result
      .then(any => this.viewCtrl.dismiss(this.careGiver))
      .catch(any => console.log("Failed to store new CareGiver"));
  }


}


export function preventDuplicatesValidatorFactory(careGiverProvider: CareGiverProvider) {

  let allExistingCareGivers: CareGiver[] = [];
  careGiverProvider.getAllCareGivers().then(careGivers => allExistingCareGivers = careGivers);
  return (c: FormControl) => {
    return preventDuplicates(c, allExistingCareGivers)
  };
}


function preventDuplicates(c: FormControl, allExistingCareGivers: CareGiver[]) {

  let input: String = c.value;
  let inputToValidate = input.toLowerCase().trim();

  let firstCareGiverWithSameName = allExistingCareGivers.find(
    careGiver => {
      return careGiver.name.toLowerCase().trim() == inputToValidate;
    }
  );

  if (typeof firstCareGiverWithSameName === "undefined") {
    return null;
  } else {
    return {
      preventDuplicates: {
        valid: false
      }
    };
  }


}



