import {Component} from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams} from 'ionic-angular';
import {EmailSettingsModalPage} from "../email-settings-modal/email-settings-modal";
import {EmailSettings, EmailSettingsProvider} from "../../providers/email-settings/email-settings";
import { CaregiversModalPage } from '../care-giver-modal/care-giver-modal';



@IonicPage()
@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private modalCtrl: ModalController,
              private provider: EmailSettingsProvider) {
  }

  ionViewDidLoad() {
  
  }

  setEmailSettings() {
    
    let storedEmailSettings = this.provider.getEmailSettings();
    storedEmailSettings.then( (emailSettings) => {
      
      let modal = this.modalCtrl.create('EmailSettingsModalPage', {emailSettings: emailSettings});
      modal.present();
      modal.onDidDismiss(data => {
        if (data) {
          let emailSettings: EmailSettings = data;
          this.provider.saveEmailSettings(emailSettings);
        }
      });
    })
    ;
  }


  adjustCareGivers() {
    let modal = this.modalCtrl.create("CaregiversModalPage", {});
    modal.present();
  }


}




