import {Injectable} from '@angular/core';
import {ProvidedCare, ProvidedCareService} from '../../providers/provided-care-service/provided-care-service';
import {CareGiver, CareGiverProvider} from '../../providers/care-giver/care-giver';
import {CareActivity, convertProvidedCareToCareActivity} from "../../pages/ProvidedCareConverter";

@Injectable()
export class LoadDataForSelectedMonthProvider {
  private allCareGiversMap: Map<String, CareGiver> = new Map<String, CareGiver>();

  constructor(
        private providedCareService: ProvidedCareService,
        careGiverProvider: CareGiverProvider) {

    careGiverProvider.getAllCareGivers().then(careGivers => {
      careGivers.forEach(careGiver => this.allCareGiversMap.set(careGiver.code, careGiver));
    });


    console.log('Hello LoadDataForSelectedMonthProvider Provider');
  }

  daysWithEvents: Map<number, Array<CareActivity>> = new Map<number, Array<CareActivity>>();

   loadCareForMonthToShow(monthToShow: Date, careGiverSelection, careTypeSelection) {
      this.daysWithEvents = new Map<number, Array<CareActivity>>();
      this.providedCareService.getStoredCare().then((storedCare) => {
        if (storedCare) {
          for (let careEvent of storedCare) {
            if (  this.shouldDisplayEvent(careEvent, monthToShow)
                  && this.hasTheRightCareGiver(careEvent, careGiverSelection)
                  && this.hasTheRightCareType(careEvent,careTypeSelection)) {
              this.addEventToDaysToDisplay(careEvent);
            }
          }
        }
      })
   }

      private shouldDisplayEvent(careEvent, monthToShow) {
        let startTime = new Date(careEvent.startTime);
        return startTime.getMonth() === monthToShow.getMonth() &&
          startTime.getFullYear() === monthToShow.getFullYear();
      }

  private hasTheRightCareGiver(careEvent, careGiverSelection) {
    if (careGiverSelection === "") {
      return true;
    }
    return careGiverSelection === careEvent.careGiver;
  }

  private hasTheRightCareType(careEvent, careTypeSelection) {
    if (careTypeSelection == null) {
      return true;
    }
    return careTypeSelection === careEvent.careType;
  }

    private addEventToDaysToDisplay(careEvent) {
        let loadedEvent : CareActivity = convertProvidedCareToCareActivity(careEvent, this.allCareGiversMap);
        let day = loadedEvent.startTime.getDate();
        if (this.daysWithEvents.has(day)) {
          this.daysWithEvents.get(day).push(loadedEvent);
        } else {
          let loadedEvents: Array<CareActivity> = [];
          loadedEvents.push(loadedEvent);
          this.daysWithEvents.set(day, loadedEvents);
        }
   }

     getDays() {
        return Array.from(this.daysWithEvents.keys()).sort((n1, n2) => n1 - n2);
     }

     getEvents(day) {
        return this.daysWithEvents.get(day);
     }
}



